import { contentEN, contentVN } from '../content/content_main';
import { BlogPostSEO } from '../content/content_blog'; // Keeping for type definition if needed, or move type here

// Blog Post Interface with SEO
export interface BlogPost {
  id: string;
  title: string;
  description: string;
  excerpt?: string;
  author: {
    name: string;
    avatar?: string;
    role?: string;
  };
  date: string;
  readTime?: string;
  image: string;
  category: string;
  slug: string;
  featured?: boolean;
  content?: string; // HTML content from rich text editor
  seo?: BlogPostSEO;
}

// Get blog posts based on language with Unsplash images
export const getBlogPosts = (lang: "en" | "vn"): BlogPost[] => {
  const source = lang === "en" ? contentEN : contentVN;
  // @ts-ignore - Accessing potentially undefined path during heavy refactor, but structure is guaranteed by metadata
  const blogPosts = source.pages.landing.section.blogContent.blogPosts || [];

  return blogPosts.map((post: any) => ({
    ...post,
    image: post.image || post.seo?.ogImage || `https://images.unsplash.com/photo-1558618666-fcd25c85cd64?w=1200&h=800&fit=crop&q=80`
  }));
};

// Get featured blog posts (for home page)
export const getFeaturedPosts = (lang: "en" | "vn"): BlogPost[] => {
  return getBlogPosts(lang).filter(post => post.featured).slice(0, 3);
};

// Get all blog posts
export const getAllPosts = (lang: "en" | "vn"): BlogPost[] => {
  return getBlogPosts(lang);
};

// Get post by slug (language-aware)
export const getPostBySlug = (slug: string, lang: "en" | "vn"): BlogPost | undefined => {
  return getBlogPosts(lang).find(post => post.slug === slug);
};

// Legacy exports for backwards compatibility (defaults to Vietnamese)
export const blogPosts = getBlogPosts("vn");
export const getPostBySlugLegacy = (slug: string) => getPostBySlug(slug, "vn");
